import React, { useState, useEffect } from "react";
import { useContext } from "react";
import { DataContext } from "../../App";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import IconButton from "@material-ui/core/IconButton";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
	deleteIcon: {
		color: "#f94144",
	},
});

const QueueItem = ({ item, index }) => {
	const classes = useStyles();

	const { setVideoQueue, admin } = useContext(DataContext);
	const [videoTitle, setVideoTitle] = useState(null);

	useEffect(() => {
		fetch(`https://noembed.com/embed?url=${item}`)
			.then((res) => res.json())
			.then((res) => {
				setVideoTitle(res.title);
			});
	}, [item]);

	const removeVideoFromQueue = () => {
		setVideoQueue((prev) => {
			return prev.filter((video, videoIndex) => index !== videoIndex);
		});
	};

	return (
		<>
			<div className="queueItem">
				<a href={item} target="_blank" rel="noopener noreferrer">
					{videoTitle ? videoTitle : item}
				</a>
				{admin && (
					<div className="remove">
						<IconButton
							className={classes.deleteIcon}
							onClick={removeVideoFromQueue}
						>
							<DeleteForeverIcon />
						</IconButton>
					</div>
				)}
			</div>
		</>
	);
};

export default QueueItem;
